const WXAPI = require('apifm-uniapp')
import store from '@/store'

/**
 * provider  alipay wxpay baidu appleiap
 * postDataExt  {}
 * money: 
 * payName: 
 * remark 
 * nextAction  null
 * successFuc 
 * failFuc 
 */
function pay(provider, postDataExt, money, payName, remark, nextAction, successFuc, failFuc) {
	const postData = {
		token: store.state.token,
		money,
		payName,
		remark,
		...postDataExt
	}
	if (nextAction) {
		postData.nextAction = JSON.stringify(nextAction)
	}














































	if (provider == 'wxpay') {
		WXAPI.wxpay(postData).then(res => {
			if (res.code != 0) {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				})
				return
			}
			uni.requestPayment({
				timeStamp: res.data.timeStamp,
				nonceStr: res.data.nonceStr,
				package: res.data.package,
				signType: res.data.signType,
				paySign: res.data.paySign,
				fail: (err) => {
					console.error(err);
					failFuc(err)
				},
				success: () => {
					successFuc()
				}
			})
		})
	}

















































}

module.exports = {
	pay: pay
}
